import navbar from '../../components/navbar/navbar.vue'
export default {
	components:{navbar},
	data() {
		return {
			show1: false,
			show2: false,
			show3: false,
			columns1: ['按贷款总额', '按房屋总价', '按房屋总价', '按房屋总价', '按贷款总额'],
			columns2: ['30年', '29年', '28年', '27年', '26年'],
			columns3: ['基本利率9折(4.40%)', '基本利率9折(4.40%)', '基本利率95折(4.66%)', '基本利率(4.9%)', '基本利率(4.9%)'],
			titleName:'房贷计算器',
			tab: 0,
			tab_list: [{
				text: '商业贷款'
			}, {
				text: '组合贷款'
			}, {
				text: '公积金贷款'
			}]
		}
	},
	methods: {
		tabList(index) {
			this.tab = index
		},
		// 开始计算
		begin(){
			uni.navigateTo({
				url:'../calculator/calculator'
			})
		},
		// popup显示隐藏
		showPopup1() {
			console.log('显示')
			this.show1 = true;
		},
		showPopup2() {
			console.log('显示')
			this.show2 = true;
		},
		showPopup3() {
			console.log('显示')
			this.show3 = true;
		},
		
		onClose() {
			console.log('隐藏')
			this.show1 = false;
			this.show2 = false;
			this.show3 = false;
		},
		// 选择器
		onConfirm1(event) {
		    const { picker, value, index } = event.detail;
		    uni.showToast({
		        title: `${index}`,
		        duration: 1000
		    });
		  },
		  onConfirm2(event) {
		      const { picker, value, index } = event.detail;
		      uni.showToast({
		          title: `${index}`,
		          duration: 1000
		      });
		    },
			onConfirm3(event) {
			    const { picker, value, index } = event.detail;
			    uni.showToast({
			        title: `${index}`,
			        duration: 1000
			    });
			  },
		onCancel() {
		   console.log('取消');
		}
	}
}
